.container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10rem;

  font-family: "Caudex";
}
.admin {
  font-size: 7rem;

  color: var(--primary-color-gold);
  text-align: center;
}
.container label {
  display: block;
  color: white;
}

.container button {
  padding: 1.5rem;
  background-color: var(--primary-color-darker);
  color: var(--primary-color-gold);
  border: none;
  cursor: pointer;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.file {
  color: white;
  font-size: 3rem;
}
.loginForm {
  text-align: center;
  font-size: 10rem;
}
.loginForm label {
  display: block;
  color: var(--primary-color-gold);
}
