.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.89);
  z-index: 12;
  display: flex;
  position: fixed;
  /* justify-content: center;
  text-align: center;
  align-items: center; */
  align-items: center;
  justify-content: center;
  text-align: center;
}
.modal p {
  display: flex;
  color: white;
  font-size: 5rem;
  margin: 5rem;
}
.modal button {
  font-size: 4rem;
  text-align: center;
  padding: 1.5rem;
  margin: 0 3rem;
  border-radius: 5rem;
  -webkit-border-radius: 5rem;
  -moz-border-radius: 5rem;
  -ms-border-radius: 5rem;
  -o-border-radius: 5rem;
  cursor: pointer;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.modal button:hover {
  font-size: 4.5rem;
}
.confirm {
  color: var(--primary-color-gold);
  background-color: transparent;
  border: 1px solid var(--primary-color-gold);
}
.cancel {
  background-color: rgb(0, 0, 0);
  color: white;
  border: 1px solid rgb(255, 255, 255);
}
