body {
  background-image: linear-gradient(
    to left,
    var(--primary-color-dark),
    rgba(0, 0, 0, 0.815)
  );
}
.container {
  height: max-content;
  width: 75%;
  display: flex;
  justify-content: space-between;
  float: right;
  padding-bottom: 5rem;
}
.sideNav {
  margin-top: 6rem;
  position: fixed;

  display: inline-block;
  height: calc(100vh - 6rem);
  width: 17.8%;
  background-image: linear-gradient(
    to left,
    rgba(12, 12, 12, 0),
    70%,
    rgba(238, 238, 238, 0.082)
  );
}
.sideNav p {
  margin-left: 1.3rem;
  transition: 0.8s;
  line-height: 2rem;
  font-size: 2.1rem;
  color: rgb(206, 206, 206);
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -ms-transition: 0.8s;
  -o-transition: 0.8s;
  font-family: "Darker Grotesque";
}
.sideNav p:hover {
  color: gold;
}

.sideNav h2 {
  margin-bottom: 2rem;
  font-size: 3.8rem;
  color: var(--primary-color-gold);
  margin-left: 2rem;
  font-weight: 400;
  font-family: "Darker Grotesque";
}
.sideNav h2:not(:last-child) {
  margin-top: 6rem;
}

.sideNav ul li {
  line-height: 1rem;
  cursor: pointer;
}
.notFound {
  color: #eee;
  margin-top: 50%;
  margin-left: 50%;
  transform: translate(-50%, -50%);
  font-size: 5rem;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.productsContainer {
  max-width: 80rem;
}
.filters {
  font-family: "Darker Grotesque";
  font-size: 3.2rem;
  color: white;
  margin-top: 5rem;
  padding: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  font-weight: lighter;
  transition: "1s";
  -webkit-transition: "1s";
  -moz-transition: "1s";
  -ms-transition: "1s";
  -o-transition: "1s";
}
@media (max-width: 700px) {
  .sideNav {
    transition: 1s;
    z-index: 5;
    display: none;
    justify-content: center;
    margin-top: 6.5rem;
    background-color: rgb(0, 0, 0);
    flex-direction: column;
    width: 35vw;
    -webkit-transition: 1s;
    -moz-transition: 1s;
    -ms-transition: 1s;
    -o-transition: 1s;
  }
  .sideNav h2 {
    font-size: 5rem;
    text-align: center;
  }
  .sideNav ul li {
    display: flex;
    justify-content: center;
    text-align: center;
    line-height: 5rem;
  }
  .container {
    width: 100vw;
    display: flex;
    justify-content: center;
  }
}
