.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.footer p {
  padding-top: 5rem;
  color: white;
  font-size: 2rem;
  max-width: 70vw;
}
.footer ul li {
  margin: 2rem;
}
@media (max-width: 700px) {
  .footer {
    height: 25vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}
