.imgContainer {
  position: relative;

  height: 100vh;
  background-size: cover;
  background-position: center;
  background-image: linear-gradient(
      to right bottom,
      rgba(14, 7, 0, 0.8),
      rgba(0, 0, 0, 0.6)
    ),
    url("../../assets/imgs/gian-d-jZxairpkhho-unsplash.jpg");
  filter: contrast(105%);
  -webkit-filter: contrast(105%);
  border-bottom: 1px solid var(--primary-color-gold);
}

.signUpFormContainer {
  color: white;

  position: absolute;
  top: 30%;
  left: 8%;
  letter-spacing: 2px;
}
.signUpFormContainer label {
  margin-bottom: 1rem;
  font-size: 3.5rem;
  text-transform: capitalize;
  display: block;
  font-family: "Darker Grotesque";
}
.signUpFormContainer input {
  background-color: rgba(0, 0, 0, 0.65);
  margin-bottom: 3.5rem;
  border: 1px solid var(--primary-color-gold);
  color: var(--primary-color-gold);
  width: 60rem;
  padding-left: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 4rem;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}
.signUpFormContainer input:-webkit-autofill,
.signUpFormContainer input:-webkit-autofill:hover,
.signUpFormContainer input:-webkit-autofill:focus,
.signUpFormContainer input:-webkit-autofill:active {
  transition: "color 9999s ease-out, background-color 9999s ease-out";
  transition-delay: 9999s;
  -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -moz-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -ms-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -o-transition: "color 9999s ease-out, background-color 9999s ease-out";
}
.btn {
  transition: 0.3s;
  color: var(--primary-color-gold);
  font-size: 2.8rem;
  left: 130%;
  top: 60%;
  width: max-content;
  position: absolute;
  padding: 1.5rem;
  border: 2px solid var(--primary-color-gold);
  background-color: transparent;
  border-radius: 3rem;
  -webkit-border-radius: 3rem;
  -moz-border-radius: 3rem;
  -ms-border-radius: 3rem;
  -o-border-radius: 3rem;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.btn:hover {
  background-color: rgba(0, 0, 0, 0.541);
}
.login .btn {
  margin-top: 14rem;
  font-size: 3rem;
  padding: 1rem 3rem;
  margin-left: 5rem;
}

.loginin {
  font-size: 2rem;
  color: white;
  left: 150%;
  top: 75%;
  position: absolute;
  width: max-content;
}
.loginin:hover {
  color: var(--primary-color-gold);
  border-bottom: 1px solid gold;
}
.goldry {
  top: 35%;
  left: 65%;
  cursor: pointer;
  position: absolute;
  font-size: 14rem;
  font-family: "Great Vibes";
  color: var(--primary-color-darkgold);
}
form p {
  padding: 0.5rem 1.5rem;
  transition: 0.5s;
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: -3rem;
  color: rgb(255, 0, 0);
  font-family: "Darker Grotesque";
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  background-color: rgba(0, 0, 0, 0.616);
  border-radius: 3rem;
  width: max-content;
  -webkit-border-radius: 3rem;
  -moz-border-radius: 3rem;
  -ms-border-radius: 3rem;
  -o-border-radius: 3rem;
}
.signUpFormContainer ul li {
  margin-top: 14rem;
}
.welcome {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background-color: var(--primary-color-darker);
}

.welcome h1 {
  color: white;
  font-size: 4.5vw;
  text-align: center;
  display: block;
}
@media (max-width: 800px) {
  .login .btn {
    margin-left: 0;
    margin-top: 0rem;
  }
  .login {
    margin-bottom: 10rem;
  }
  .goldry {
    position: absolute;
    top: 20%;
    font-size: 18rem;
    left: 50%;
    transform: translate(-50%, -20%);
    -webkit-transform: translate(-50%, -20%);
    -moz-transform: translate(-50%, -20%);
    -ms-transform: translate(-50%, -20%);
    -o-transform: translate(-50%, -20%);
  }
  .signUpFormContainer {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -60%);
    -webkit-transform: translate(-50%, -60%);
    -moz-transform: translate(-50%, -60%);
    -ms-transform: translate(-50%, -60%);
    -o-transform: translate(-50%, -60%);
  }
  .btn {
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -90%);
    -webkit-transform: translate(-50%, -90%);
    -moz-transform: translate(-50%, -90%);
    -ms-transform: translate(-50%, -90%);
    -o-transform: translate(-50%, -90%);
  }
  .loginin {
    font-size: 2.5rem;
    color: white;
    position: relative;
    width: max-content;
  }
}

@media (max-width: 700px) {
  .goldry {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -20%);
    -webkit-transform: translate(-50%, -20%);
    -moz-transform: translate(-50%, -20%);
    -ms-transform: translate(-50%, -20%);
    -o-transform: translate(-50%, -20%);
  }
  .signUpFormContainer {
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -60%);
    -webkit-transform: translate(-50%, -60%);
    -moz-transform: translate(-50%, -60%);
    -ms-transform: translate(-50%, -60%);
    -o-transform: translate(-50%, -60%);
  }
  .btn {
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -90%);
    -webkit-transform: translate(-50%, -90%);
    -moz-transform: translate(-50%, -90%);
    -ms-transform: translate(-50%, -90%);
    -o-transform: translate(-50%, -90%);
  }
  .loginin {
    font-size: 2.5rem;
    color: white;
    position: relative;
    width: max-content;
  }
}
