:root {
  --primary-color-darker: #070707;
  --primary-color-gold: #ffc400;
  --primary-color-darkgold: #ffc400df;
  --primary-color-dark: #121212;
  --primary-color-lighter: #242424;
  --primary-color-grey: #eee;
}
@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caudex:wght@700&family=Cinzel+Decorative:wght@400;700&family=Darker+Grotesque:wght@300&family=Gabriela&family=Lusitana&display=swap");

* {
  margin: 0;
  padding: 0;
  font-size: 62.25%;
}
*::selection {
  background-color: var(--primary-color-gold);
  color: var(--primary-color-darker);
}
body {
  background-color: var(--primary-color-dark);
  font-family: "Cormorant Garamond";
}

h1,
p {
  padding: 2rem;
}
h1 {
  font-size: 4.5rem;
  font-weight: bold;
  padding-bottom: 5rem;
}
p {
  font-size: 3rem;
}

.iconify {
  cursor: pointer;
  color: white;
  font-size: 4.2rem;
  margin-left: 2rem;
  margin-right: 3rem;
}
.iconify--mdi:hover {
  color: #bb001b;
}
.iconify--fa-brands:hover {
  color: #006aff;
}

.iconify--fa-brands {
  font-size: 3.5rem;
}
.iconify--simple-icons:hover {
  color: #007fed;
}
.iconify--cib:hover {
  color: #6fda44;
}
.ionicon .s-ion-icon {
  font-size: 4rem;
  color: var(--primary-color-gold);
}
.iconify--mdi:not(:first-child) {
  font-size: 5rem;
  color: var(--primary-color-gold);
}
.iconify--mdi:not(:first-child):hover {
  color: gold;
}
.iconify--bi {
  font-size: 10rem;
  color: lightgreen;
}
.iconify--ic {
  margin: 0;
  margin-top: 0.3rem;
  font-size: 3.5rem;
  color: #eee;
}
.iconify--icons8 {
  color: white;
  position: absolute;
  font-size: 5rem;
  margin: 2rem;
}
.iconify--icons8:hover {
  color: var(--primary-color-gold);
}
.headerText {
  color: var(--primary-color-gold);
  font-size: 8rem;
}
.menu {
  color: #eee;
  position: fixed;
  font-size: 5rem;
  z-index: 3;
  display: none;
  margin-top: 10rem;
  transition: 1s;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}
.x {
  color: white;
  font-size: 5rem;
  display: none;
}
.x2 {
  position: absolute;
  font-size: 3rem;
  color: white;
  margin: 1rem;
}
.arrow {
  font-size: 5rem;
  color: var(--primary-color-gold);
}
@media (max-width: 1120px) {
  * {
    font-size: 50%;
  }
}
@media (max-width: 900px) {
  * {
    font-size: 45%;
  }
}
@media (max-width: 810px) {
  * {
    font-size: 40%;
  }
}

@media (max-width: 700px) {
  * {
    font-size: 35%;
  }
  .menu,
  .x {
    display: flex;
  }
}
@media (max-width: 600px) {
  * {
    font-size: 26%;
  }
}
