.section2 {
  height: 60vh;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.92),
      rgba(0, 0, 0, 0.92)
    ),
    url("./Jewellery-Store--1024x768.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
  border-bottom: 1px solid var(--primary-color-gold);
}
.section2 h1 {
  width: max-content;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--primary-color-gold);
  font-size: 7rem;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}
.section2 span {
  color: white;
  font-size: 10rem;
}
.earth {
  height: 75vh;
  background-image: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.712),
      rgba(0, 0, 0, 0.995)
    ),
    url("../../../assets/imgs/United_Space_in_Europe_pillars.png");
  background-size: cover;
  background-position: center;
  position: relative;
  background-attachment: fixed;
  border-bottom: 1px solid var(--primary-color-gold);
}
.earth h1 {
  width: max-content;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--primary-color-gold);
  font-size: 7rem;

  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}
.earth span {
  font-size: 7rem;
  padding: 1rem;
  color: var(--primary-color-darker);
  background-color: var(--primary-color-gold);
}

.signUp {
  overflow: hidden;
  background-size: cover;
  background-position: top;
  position: relative;
  height: 70vh;
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.8),
      rgba(0, 0, 0, 0.502)
    ),
    url("../../../assets/imgs/render-diamond-crown-expensive-grind.jpg");
  border-bottom: 1px solid var(--primary-color-gold);
}
.signUp h1 {
  color: white;
  font-size: 6.5rem;
  width: 40vw;
}
.textContainer {
  position: absolute;
  margin-top: 15%;
  margin-left: 5%;
  display: flex;

  align-items: flex-end;
}
.signUp span a {
  font-size: 8rem;
  color: var(--primary-color-gold);
}
.signUp span a:hover {
  border-bottom: 2px solid var(--primary-color-gold);
}

.textContainer a:not(:first-child) {
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 3.5rem;
  background-color: var(--primary-color-gold);
  color: black;
  padding: 1rem 2rem;
  border-radius: 10rem;
  -webkit-border-radius: 10rem;
  -moz-border-radius: 10rem;
  -ms-border-radius: 10rem;
  -o-border-radius: 10rem;
  margin-bottom: 6rem;
  margin-right: 5rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.textContainer a:not(:first-child):hover {
  color: var(--primary-color-gold);
  background-color: black;
  border: 1px solid var(--primary-color-gold);
  font-weight: bold;
}

@media (max-width: 800px) {
  .earth {
    height: 30vh;
  }
  .section2 {
    height: 30vh;
  }
  .signUp {
    height: 35vh;
  }
}

@media (max-width: 700px) {
  .earth {
    height: 30vh;
  }
  .signUp {
    height: 35vh;
  }
  .section2 {
    height: 30vh;
  }
  .section2 h1 {
    font-size: 4rem;
  }
  .section2 span {
    font-size: 6rem;
  }
  .earth {
    height: 35vh;
  }
  .earth h1 {
    font-size: 4.5rem;
  }
  .earth span {
    font-size: 5rem;
  }
  .signUp h1 {
    font-size: 5rem;
  }
  .textContainer h1 span a {
    font-size: 6.5rem;
  }
}
