h1,
h2 {
  letter-spacing: 0.4rem;
}
.header {
  border-bottom: 1px solid var(--primary-color-gold);
  height: 70vh;
  max-width: 100vw;
}
.img {
  background-image: url(../../../assets/imgs/header-img.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  filter: brightness(15%);
  -webkit-filter: brightness(15%);
  height: inherit;
  position: relative;
  background-attachment: fixed;
}
.textContainer {
  position: absolute;
  top: 35%;
  left: 5%;
  display: block;
  line-height: 2rem;
}
.headerText {
  color: var(--primary-color-gold);
  font-size: 8rem;
}
.subHeaderText {
  text-transform: capitalize;
  color: white;
  font-size: 3rem;
  padding-left: 2.5rem;
}
.trustedBy {
  font-size: 3rem;
  position: absolute;
  top: 23%;
  left: 67%;

  color: rgba(206, 205, 205, 0.911);
  width: max-content;
}
.logos {
  position: absolute;
  max-width: 50rem;
  display: flex;
  top: 30%;
  left: 60%;
}
.logo1,
.logo2,
.logo3,
.logo4 {
  margin-right: 5rem;

  flex: 1;
}
.logo1 {
  filter: contrast(65%);
  width: 17rem;
  -webkit-filter: contrast(65%);
}
.logo2 {
  padding-top: 2rem;
  width: 18rem;
}
.logo3 {
  margin-left: 5rem;
  width: 28rem;
}

