@media (max-width: 800px) {
  .header {
    height: 40vh;
  }
  .logos {
    top: 20%;
  }
  .trustedBy {
    top: 15%;
  }
  .textContainer {
    top: 20%;
  }
}
@media (max-width: 720px) {
  .header {
    height: 50vh;
  }
  .textContainer {
    position: absolute;
    text-align: center;
    line-height: 4rem;
    top: 20%;
    left: 50%;
    transform: translate(-50%, 0);
    -webkit-transform: translate(-50%, 0);
    -moz-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
  }
  .logos {
    position: absolute;
    top: 35%;

    left: 60%;
    width: 100%;
    transform: translate(-60%, 0);
    -webkit-transform: translate(-60%, 0);
    -moz-transform: translate(-60%, 0);
    -ms-transform: translate(-60%, 0);
    -o-transform: translate(-60%, 0);
  }
  .logo1,
  .logo2,
  .logo3 {
    left: auto;
    right: auto;
    display: block;
    display: none;
  }
  .trustedBy {
    position: absolute;
    display: none;
  }
  .headerText {
    width: max-content;
    font-size: 15rem;
  }
  .subHeaderText {
    font-size: 3rem;
  }
}
